@import '../../styles/all';

.welcomeContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  .welcomeContentContainer {
    .welcomeContentOverlay {
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      display: flex;
      opacity: 0.86;
      background-color: $colorPrimary;
    }

    .welcomeContent {
      height: 100%;
      width: 85%;
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: center;

      @media (min-width: $screen-sm) {
        align-items: flex-start;
      }

      .welcomeTitle {
        margin-top: 53px;
        font-size: 40px;
        font-family: $fontBold;
        line-height: 46px;
        color: white;

        @media (min-width: $screen-sm) {
          font-size: 55px;
          line-height: 54px;
        }

        .welcomeTitleHighlight {
          color: $colorYellow;
        }
      }

      .welcomeSmallPrint {
        width: 100%;
        margin: 25px 0 36px 0;
        font-size: 13px;
        font-family: $fontRegular;
        line-height: 19.6px;
        letter-spacing: 0;
        color: white;

        @media (min-width: $screen-sm) {
          display: none;
        }
      }
    }
  }

  .welcomeAuthContainer {
    display: flex;
    width: 100%;
    max-width: 600px;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    background-color: $colorWhite;
    z-index: $zIndexWelcomAuthContainer;

    .verifyBlock {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      height: 100%;
      vertical-align: middle;

      .successIcon {
        fill: $colorGreen;
        width: 100px;
        height: 100px;
      }

      .failedIcon {
        fill: $colorRed;
        width: 100px;
        height: 100px;
      }

      .shortcutText {
        font-size: 32px;
        margin-top: 50px;
        text-align: center;
      }

      .resendEmailButton {
        background-color: $colorWhite;
        width: 100%;
        height: 60px;
        font-size: 20px;
        background-image: url('../../data/images/signup-background-one.svg');
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        bottom: 35px;

        @media (min-width: $screen-sm) {
          width: 70%;
        }
      }
    }
  }
}
